<template>
  <div class="indexHtml">
    <van-nav-bar :title="name+' 聊天'" left-text="返回" left-arrow @click-left="onClickLeft">
      <van-icon name="contact" slot="right" />
    </van-nav-bar>
    <div class="window">

    </div>
    <van-tabbar>
      <van-tabbar-item>录音</van-tabbar-item>
      <van-tabbar-item>
        <textarea></textarea v-model="content">
      </van-tabbar-item>
      <van-tabbar-item @click="addNews">发送</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
$("title").text("我的好友");
import { Toast } from "vant";
import { List } from "vant";
//import "@/assets/js/websocket.js";

export default {
  name: "indexHtml",
  data() {
    let _this = this;
    return {
      user: null,
      msg: "",
      input: "",
      message: "",
      list: [],
      loading: true,
      finished: false,
      client_id: _this.$route.params.id,
      group: "sgq",
      name: _this.$route.params.name
    };
  },
  created: function() {
    let _this = this;
    if (localStorage.user) {
      _this.user = JSON.parse(localStorage.user);
    } else {
      _this.utilMsg("请先登录");
      return false;
    }
  },
  methods: {
    //组
    getGroup: function() {
      let _this = this;
      $.ajax({
        type: "GET",
        async: false,
        url:
           _this.apiUrl + "/e/wxapi/addToGroup.php?client_id=" +
          this.client_id +
          "&liaotianId=" +
          this.group,
        dataType: "json",
        success: function(result) {
          alert(result);
        }
      });
    },
    //添加消息
    addNews: function() {
      let _this = this;
      this.loading = true;
      $.ajax({
        type: "POST",
        url: _this.apiUrl + "/e/wxapi/sendToDesk.php",
        datatype: "json",
        data: {
          userId: _this.user.userid,
          username: _this.user.username,
          group: _this.group,
          content: _this.content,
          headpic: "", //头像
          nickname: "呵呵", //昵称
          qunliao_id: -1, // no: -1  yes: 群聊id
          send_to_user_id: this.$route.params.id || 0
        },
        success: function(res) {
          console.log(res);
          //_this.list = $.parseJSON(res);
          console.log(_this.list);
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
          _this.utilMsg("请求失败");
        }
      });
    },
    onClickLeft: function() {
      this.$router.go(-1);
    },
    websocket: function() {
      let _this = this,ws = new WebSocket("ws://39.108.60.146:8282"),
        ws_bind = function(client_id) {
          //绑定
          $.ajax({
            type: "GET",
            url:  _this.apiUrl + "/e/wxapi/GateWayBind.php",
            data: {
              client_id: client_id
            },
            success: function(result) {
              var a = 1;
            }
          });
        },
        page_index = function() {
          /*取会员所在页面*/
          var page_index = $("title").text();
          return page_index;
        },
        get_info = function(data_type) {
          //发送用户信息
          page_index = page_index();
          user = $("#username").text();
          var user_info = {
            user: user,
            data_type: data_type,
            page_index: page_index,
            data: {
              a: "b"
            }
          };
          if (ws.readyState == 1) {
            ws.send(user_info);
            return true;
          }
        };
      ws.onmessage = function(e) {
        console.log(e);
        var data = {};
        if (e.data.indexOf("{") >= 0) {
          data = eval("(" + e.data + ")");
          _this.client_id = data.client_id;
        }
        if (data.type == "bind") {
          ws_bind(data.client_id);
        }
        if (data.type == "game") {
          if (data.data_type == "base") {
            var funcStr = "";
            var args = "";
            var info = eval("(" + data.info + ")");
            for (var key in info) {
              if (key == "args") {
                args = info[key] + "";
                continue;
              }
            }
            funcStr = info["a"] + "('" + args + "')";
            eval("(" + funcStr + ")");
          }
          if (data.data_type == "alert") {
            //alert(data.info.[0]);
          }
        }
      };
    }
  },
  mounted() {}
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.van-nav-bar .van-nav-bar__title {
  max-width: 200px;
  margin: 0 auto;
  padding-top: 8px;
}
</style>



