<template>
  <div class="contentHtml">
  	<van-tabs @click="handleTabClick">
	  <van-tab v-for="titleText in titleObj" :title="titleText"></van-tab>
      <div class="mb10"></div>
      <!--资讯＝0-->
  	  <div class="listBox0 listBoxs" :class="{ isshow : active==0 }">
  	  	<div class="text-c initBox mb10" v-on:click="hrefTo('/post/add','发布资讯')"><img class="textImg" src="../../assets/zixun@2x.png" width="13">  发布资讯</div>
	  	<ul>
	  	  <template v-for="listData in listData0">
  			<li v-on:click="hrefTo(listData.id)">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div class="listInfo">
	    					<span>{{listData.username}}&emsp;{{listData.newstime | timeFormatter}}&emsp;阅读:{{listData.onclick}}</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.titlepic"></div>
	    		</li>
	    	 </template>
	    </ul>
	    <div class="lodingDiv loding"></div>
      </div>
      <!--课堂＝1-->
      <div class="listBox1 listBoxs" :class="{ isshow : active==1 }">
      	<div class="text-c initBox mb10" v-on:click="hrefTo('/index/classroom/classroomAdd','发布课堂')"><img class="textImg" src="../../assets/fabuketang@3x.png" width="15">  发布课堂</div>
	  	<ul class="listBox">
	  	  <template v-for="listData in listData1">
  			<li v-on:click="hrefTo('/index/classroom/classroomDetail/'+listData.id)">
	  			<div class="rightBox"><img v-bind:src="listData.lesson_head_img"></div>
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div class="listInfo">
	    					<img v-bind:src="listData.lesson_head_img" class="textImg" width="20" height="20">
	    					<span class="but1">哈哈</span>&emsp;
	    					<span>{{listData.username}}&emsp;</span>
	    					<span class="fr">{{listData.focus_num}}人</span>
	    				</div>
	    			</div>
	    		</li>
	    	 </template>
	    </ul>
	    <div class="lodingDiv loding"></div>
      </div>
      <!--问答＝2-->
      <div class="listBox2 listBoxs" :class="{ isshow : active==2 }">
      	<div class="text-c initBox mb10" v-on:click="hrefTo('/index/answer/answerAdd','发布问答')"><img class="textImg" src="../../assets/tiwen@2x.png" width="16">  发布问答</div>
	  	<ul>
	  	  <template v-for="listData in listData2">
  			<li class="mb10 initBox" v-on:click="hrefTo('hrefTo','/index/answer/answerDetail/'+listData.id)">
	  			<div class="initTitle">{{listData.title}}</div>
	  			<div class="color6 font14"><van-tag plain type="danger">标签</van-tag> 时间：{{listData.time}}</div>
	  		</li>
	    	 </template>
	    </ul>
	    <div class="lodingDiv loding"></div>
      </div>
      <!--网店＝3-->
      <div class="listBox3 listBoxs" :class="{ isshow : active==3 }">
      	<div class="text-c initBox mb10" v-on:click="hrefTo('/index/transfer/transferredDetail','发布网店')"><img class="textImg" src="../../assets/chushouwangdian@2x2.png" width="15">  发布网店</div>
	  	<ul class="listBox">
	  	  <template v-for="listData in listData3">
  			<li v-on:click="hrefTo('hrefTo','/index/transfer/shopInfo/'+listData.id,'网店详情')">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div class="colorA">{{listData.price2}}</div>
	    				<div class="listInfo">
	    					<template v-for="tag in listData.tags">
	    						<span class="but1">{{tag}}</span>&emsp;
	    					</template>
	    					<span>类目：{{listData.shop_type }}&emsp;发布时间： {{listData.intime}}&emsp;</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.shop_img_url"></div>
	    		</li>
	    	 </template>
	    </ul>
	    <div class="lodingDiv loding"></div>
      </div>
      <!--竞价＝4-->
      <div class="listBox4 listBoxs" :class="{ isshow : active==4 }">
	  	<div class="text-c initBox mb10" v-on:click="hrefTo('/index/bidding/biddingAdd','发布竞价')"><img class="textImg" src="../../assets/fabujingjia@2x.png" width="15">  发布竞价</div>
	  	<ul>
	  	  <template v-for="listData in listData4">
  			<li class="mb10 initBox lineHeight">
		  			<div style="margin-bottom: 5px;" v-on:click="hrefTo('/index/bidding/biddingDetail/'+listData.id,'竞价详情')">
			  			<div class="initTitle">{{listData.title}}</div>
			  			<div class="color9">{{listData.content}}</div>
		  			</div>
		  			<div class="color9">
			  			<div class="flex"><span class="colorA">¥{{listData.price}}</span><span>{{listData.buynum}}人参与｜竞价</span></div>
			  			<div class="flex"><span>已托管</span><span>{{listData.stop_time}}截止</span></div>
		  			</div>
		    		</li>
	    	 </template>
	    </ul>
	    <div class="lodingDiv loding"></div>
      </div>
      <!--招聘＝5-->
      <div class="listBox5 listBoxs" :class="{ isshow : active==5 }">
      	<div class="text-c initBox mb10" v-on:click="hrefTo('/index/recruit/recruitAdd','发布招聘')"><img class="textImg" src="../../assets/fabuzhaopin@2x.png" width="10">  发布招聘</div>
      	
	  	<ul class="listBox">
	  	  <template v-for="listData in listData5">
  			<li v-on:click="hrefTo('hrefTo','/index/transfer/shopInfo/'+listData.id,'网店详情')">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title}}</div>
	    				<div class="colorA">{{listData.price2}}</div>
	    				<div class="listInfo">
	    					<template v-for="tag in listData.tags">
	    						<span class="but1">{{tag}}</span>&emsp;
	    					</template>
	    					<span>类目：{{listData.shop_type }}&emsp;发布时间： {{listData.intime}}&emsp;</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.shop_img_url"></div>
	    		</li>
	    	 </template>
	    </ul>
	    <div class="lodingDiv loding"></div>
      </div>
	    
	</van-tabs>
	
  </div>
</template>

<script>
export default {
  name: 'contentHtml',
  data () {
    return {
    		titleObj:["资讯","课堂","问答","网店","竞价","招聘"],
    		active: 0,
    		listData0:[],
    		listData1:[],
    		listData2:[],
    		listData3:[],
    		listData4:[],
    		listData5:[],
    }
  },
  created:function(){
  	var _this = this;
 	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  var active = this.active;
	  if(top+windowH==height){
	  	var addHtml = $(".listBox"+active+" .lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  	
  },
  methods: {
    handleTabClick(index) {
    		this.active = index;
    		this.getList();
    },
	hrefTo(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title,}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val)
  		}
	},
    getList:function(){
	    	var _this = this;
		if(localStorage.user){
	 		var user_id =  JSON.parse(localStorage.user).userid;
	 	}else{
	 		_this.utilMsg('请先登录');
	 		return false;
	 	}
    		var active = _this.active;
		if(active==0){
			//请求资讯
			_this.getList0(user_id);
		}else if(active==1){
			//请求课堂
			_this.getList1(user_id);
		}else if(active==2){
			//请求问答
			_this.getList2(user_id);
		}else if(active==3){
			//请求网店
			_this.getList3(user_id);
		}else if(active==4){
			//请求竞价
			_this.getList4(user_id);
		}else if(active==5){
			//请求招聘
			_this.getList5(user_id);
		}
    },
    getList0:function(user_id){
    		var _this = this;
    		$(".listBox0 .lodingDiv").text('').addClass("loding").show();
    		var per_page = 6;
    		var start = _this.listData0.length;
    		var data = {
	    		"fk_user_id":user_id,
	        'moduleid':1,    //模型ID (moduleid 与 classid 其中一项为必须)
	        'classid':2,         //栏目ID (moduleid 与 classid 其中一项为必须)
	        'start':start,          //开始显示条数 (分页时用，默认为0)
	        "limit":per_page,           //显示总条数 (默认为15)
	        "order":"newstime desc", //排序 (如 newstime desc)
	        "keywor":''
		}
	  	$.post(_this.apiUrl+'/e/wxapi/list.php',data,function(res){
	  		$(".listBox0 .lodingDiv").hide();
			for(var i = 0; i < res.length; i++) {
		      _this.listData0[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".listBox0 .lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},"json")
    },
    getList1:function(user_id){
    		var _this = this;
  		$(".listBox1 .lodingDiv").text('').addClass("loding").show();
    		var per_page = 20;
    		var start = _this.listData1.length;
    		var datas = {
    			"fk_user_id":user_id,
	        "username":"",
			"type":-1,
			"is_hot":-1,
			"end_time":"",
			"status":1,
			"title":"",
			"pageIndex":start,
			"pageSize":per_page,
		}
	  	$.get(_this.apiUrl+'/e/wxapi/lesson_list.php',datas,function(res){
	  		$(".listBox1 .lodingDiv").hide();
			for(var i = 0; i < res.length; i++) {
		      _this.listData1[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".listBox1 .lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},'json')
    },
    getList2:function(user_id){
    		var _this = this;
  		$(".listBox2 .lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var start = _this.listData2.length;
    		var per_page = 10;
    		var datas = {
    			"is_hot":-1,
    			"fk_user_id":user_id,
    			"pageSize":per_page,
    			"pageIndex":0,
    			"title":'',
    			"status":start,
    		}
	  	$.get(_this.apiUrl+'/e/wxapi/my_ask_list.php',datas,function(res){
	  		$(".listBox2 .lodingDiv").hide();
			for(var i = 0; i < res.length; i++) {
		      _this.listData2[start+i] = res[i];
		      _this.listData2[start+i].key = start+i;
		   }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".listBox2 .lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},'json')
    },
    getList3:function(user_id){
    		var _this = this;
  		$(".listBox3 .lodingDiv").text('').addClass("loding").show();
    		var start = _this.listData3.length;
    		var per_page = 20;
    		var data = {
  			"fk_user_id":user_id,
	        'pageIndex':start,
	        'pageSize':per_page,
	        'username':"",   //搜索字段店铺所属用户名
	        'nickname':"",   //搜索字段，店铺昵称
	        'public_time_start':"",  //搜索字段发布时间开始
	        'pulbic_time_end ':"",  //搜索字段发布时间结束
	        'shop_type':-1,  //搜索字段，店铺类型
	        'shop_addr':-1,  //搜索字段，店铺地区
	        'title':"",   //搜素字段，店铺标题
	        'sh_status':1  //搜索审核通过的，0为未审核通过
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/shoplist.php',data,function(res){
	  		$(".listBox3 .lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listData3.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listData3[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".listBox3 .lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},'json')
    },
    getList4:function(user_id){
    		var _this = this;
  		$(".listBox4 .lodingDiv").text('').addClass("loding").show();
    		var start = _this.listData4.length;
    		var per_page = 6;
    		var obj = {
    			"fk_user_id":user_id,
			"username":'',
			"type": -1 ,
			"title":"",
			"budget_end":-1,
			"num":'',
			"time":'',
			"is_top":-1,
			"status":-1,
			"examine":1,
			"pageIndex":start,
			"pageSize":per_page
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/demand_list.php',obj,function(res){
	  		$(".listBox4 .lodingDiv").hide();
			for(var i = 0; i < res.length; i++) {
		      _this.listData4[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".listBox4 .lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},"json")
    	
    },
    getList5:function(user_id){
    		$(".listBox5 .lodingDiv").text("本功能开发中").removeClass("loding").show();
    }
  }
}
</script>

<style scoped>
	.listBoxs{display: none;}
	.isshow{display: block;}
/*资讯*/
.listBox0 li{padding:10px;border-bottom: 1px solid #eee; background: #fff;overflow: hidden;display: flex;justify-content:space-between}
.listBox0 li a{display: flex;display: -webkit-flex; /* Safari */justify-content:space-between;}
.listBox0 li .leftBox{float: left;}
.listBox0 li .leftBox .listInfo{color: #999;font-size: 1rem;margin-top: 0.5rem;}
.listBox0 li .leftBox .listInfo .but1{border:1px solid #eee;font-size:1rem; margin-right: 5px;color: #ed7655; padding: 0px 6px;border-radius:5px;-webkit-border-radius:5px;}
.listBox0 li .leftBox .listInfo .but2{border:1px solid #ed7655;background: #ed7655;color: #fff;}
.listBox0 li .rightBox{float: right;}
.listBox0 li .rightBox img{width: 10rem;height: 7rem;}
.listBox0 li .listTitle{font-size: 14px;color: #333;height:60px;}

/*课堂*/
.listBox1 li{justify-content:flex-start;}
.listBox1 .rightBox{margin-right: 10px;}
.listBox1 li .listTitle{font-size: 14px;height: 40px;}
.listBox1 .leftBox .listInfo img{border-radius: 50%;-webkit-border-radius: 50%;}

/*问答*/
.listBox2 li .listTitle{font-size: 14px;height: 40px;}
/*网店*/
.listBox3 li{padding:10px;border-bottom: 1px solid #eee; background: #fff;overflow: hidden;display: flex;justify-content:space-between}
.listBox3 li a{display: flex;display: -webkit-flex; /* Safari */justify-content:space-between;}
.listBox3 li .leftBox{float: left;}
.listBox3 li .leftBox .listInfo{color: #999;font-size: 1rem;margin-top: 0.5rem;}
.listBox3 li .leftBox .listInfo .but1{border:1px solid #eee;font-size:1rem; margin-right: 5px;color: #ed7655; padding: 0px 6px;border-radius:5px;-webkit-border-radius:5px;}
.listBox3 li .leftBox .listInfo .but2{border:1px solid #ed7655;background: #ed7655;color: #fff;}
.listBox3 li .rightBox{float: right;}
.listBox3 li .rightBox img{width: 10rem;height: 7rem;}
.listBox3 li .listTitle{font-size: 14px;color: #333;height:60px;}
	
	
</style>



