<template>
  <div class="indexHtml">
    <div data-v-645028cc="" class="van-nav-bar van-hairline--top-bottom" style="z-index: 1;">
      <div class="van-nav-bar__title">
        <div class="van-tabs__nav van-tabs__nav--card">
          <div class="van-tab van-tab--active">
            <span class="van-ellipsis">我的消息</span>
          </div>
          <div class="van-tab">
            <router-link to="/friends" class="van-ellipsis">我的好友</router-link>
          </div>
        </div>
      </div>
      <div class="van-nav-bar__right">
        <i class="van-icon van-icon-add-o" @click="addModal = true;"></i>
      </div>
    </div>
    <van-actionsheet v-model="addModal" :actions="actions" cancel-text="取消" />
    <van-search placeholder="搜索" v-model="search" />
    <div class="list-main">
      <van-cell v-for="item in list">
        <div class="list-col" @click="hrefTo('/chatWindow/' + item.group + '/' + item.send_to_ids,'与 ' + item.nickname1 + ' 聊天')">
          <div class="img"><img :src="item.head_img" /></div>
          <div>{{item.nickname1}}</div>
          <div class="right">
            {{item.time}}
          </div>
        </div>
      </van-cell>
    </div>
  </div>
</template>
<script>
$("title").text("我的消息");
import { Toast } from "vant";
import { List } from "vant";
import { CellSwipe } from "vant";
export default {
  name: "indexHtml",
  data() {
    let _this = this;
    return {
      addModal: false,
      actions: [
        {
          name: "发起群聊",
          callback: function(){
            _this.hrefTo("/addgroupchat","发起群聊");
          }
        },
        {
          name: "添加好友",
          callback: function(){
            _this.hrefTo("/addfriends","添加好友");
          }
        },
        {
          name: "消息设置",
          callback: function(){
            _this.hrefTo("/messagesettings","消息设置");
          }
        }
      ],
      search: "",
      user: {},
      msg: "",
      input: "",
      message: "",
      list: [],
      loading: true,
      finished: false
    };
  },
  created: function() {
    let _this = this;
    if (localStorage.user) {
      _this.user = JSON.parse(localStorage.user);
    } else {
      _this.utilMsg("请先登录");
      return false;
    }
    _this.getList();//消息列表
  },
  methods: {
    hrefTo(val, title) {
      if (this.utilIsLocalhost() == false) {
        var content = { type: "hrefTo", link: val, title: title };
        var data = JSON.stringify(content);
        ttf.nslog(data);
      } else {
        this.$router.push(val);
      }
    },
    //消息列表
    getList: function() {
      var _this = this;
      this.loading = true;
      $.ajax({
        type: "GET",
        url: _this.apiUrl + "/e/wxapi/xiaoxi_list.php", // 接口返回JSON格式的数据
        cache: false,
        datatype: "json",
        data: { fk_user_id: 20 || _this.user.userid },
        success: function(res) {
          _this.list = $.parseJSON(res);
          this.loading = false;
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
          _this.utilMsg("请求失败");
        }
      });
    },
    toUrl: function() {
      this.$router.push({
        name: "chatWindow",
        params: { userId: this.user["userid"] }
      });
    },
    onClose(clickPosition, instance) {
      switch (clickPosition) {
        case "left":
        case "cell":
        case "outside":
          instance.close();
          break;
        case "right":
          Dialog.confirm({
            message: "确定删除吗？"
          }).then(() => {
            instance.close();
          });
          break;
      }
    }
  },
  mounted() {
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  background-color: rgb(242, 242, 242);
}
.van-nav-bar .van-nav-bar__title {
  max-width: 200px;
  margin: 0 auto;
  padding-top: 8px;
}
.list-main {
  padding: 5px 0;
  background-color: #fff;
}
.list-main .van-cell{
  padding: 5px 15px;
}
.list-main ~ .list-main{
  margin-top: 10px;
}
.list-col {
  position: relative;
  min-height: 50px;
  padding: 0 50px 0 60px;
  line-height: 50px;
}
.list-col .img {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 50px;
  background-color: #ddd;
  overflow: hidden;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  font-size: 0;
}
.list-col img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}
.list-col .right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}
</style>


