<template>
  <div class="contentHtml">
  	  <ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<!--<li v-on:click="hrefTo(listData.id)">-->
	  		<li v-on:click="purchase(listData.id)">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.name}}</div>
	    				<div class="listInfo">
	    					<span>价格:{{listData.price}}</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.titlepic"></div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("资讯");
import { Dialog } from 'vant';
export default {
  name: 'contentHtml',
  data () {
    return {
	    	listDatas:[],
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var per_page = 20;
    		var start = _this.listDatas.length;
    		var data = {
        "pageIndex":start,
		"pageSize":per_page
		}
	  	$.get(_this.apiUrl+'/e/wxapi/shang_pin.php',data,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
			}
		},"json")
  	},
  	purchase:function(id){
  		var _this = this;
		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		_this.utilMsg("请先登录");
  	 		return false;
  	 	}
    		var data = {
			'fk_user_id':user_id,
        		'fk_buy_item_id':id
		}
    		$("#msgLoding").show();
	  	$.get(_this.apiUrl+'/e/wxapi/sp_dd.php',data,function(res){
	  		$("#msgLoding").hide();
	  		console.log(res)
	  		//下单成功
	  		if(res.status==1){
	  			//立即付款
	  			Dialog.confirm({
				  title: '',
				  message: '确认选择这个项目？',
				  confirmButtonText:'立即付款',
				  cancelButtonText:'考虑考虑'
				}).then(() => {
					var obj = {
						'fk_user_id':user_id,
        					'fk_buy_item_id':id
					};
					$("#msgLoding").show();
					$.get(_this.apiUrl+'/e/wxapi/sp_fk.php',obj,function(res){
			  			$("#msgLoding").hide();
			  			_this.utilMsg(res.content);
					},"json")
				})
	  		}else{_this.utilMsg(res.content);}
	  		
		},"json")
  	},
	hrefTo(id){//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":"/information/"+id,"title":"资讯详情"}
		    	var data = JSON.stringify(content);
		    	alertTtf(data)
	  	}else{
	  		this.$router.push('/information/'+id)
	  	}
	},
	
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



