<template>
  <div class="textHtml">
  	<van-cell-group>
	  <van-field v-model="username" label="真实姓名" icon="clear" placeholder="请输入" required @click-icon="username = ''"/>
	  <van-field v-model="id_num" label="身份证号" placeholder="请输入" required/>
	</van-cell-group>
	<div class="addList" style="padding-bottom:80px;">
		<h3 class="imgH3" data-id0="" data-id1="" data-id2="">
  			<span style="color:red;">*</span>
  			<span class="inputLeft">封面:</span>
  		</h3>
  		<div style="color:#ccc;">&nbsp;&nbsp;&nbsp;身份证照片清晰可看，能看见证件号码</div>
  		<van-uploader :after-read="onRead0">
  			<div class="butIcons">身份证正面</div>
		</van-uploader>
		<van-uploader :after-read="onRead1">
  			<div class="butIcons">身份证反面</div>
		</van-uploader>
		<van-uploader :after-read="onRead2">
  			<div class="butIcons">手持身份证</div>
		</van-uploader>
    </div>
	<van-cell-group>
	  <van-cell title="支付宝绑定" is-link v-on:click="alipayFn(true)" />
	  <van-cell title="银行卡绑定" is-link v-on:click="bankCardFn(true)" />
	</van-cell-group>
	<div class="butAdd but-80-red" style="margin: 40px auto;"  v-on:click="certification">提交</div>
	
	<van-popup v-model="alipay" position="bottom" :overlay="true">
		<!--支付宝绑定-->
		<div >
			<h2 style="text-align:center;">支付宝绑定</h2>
			<van-cell-group>
			  <van-field v-model="username" label="姓名" icon="clear" placeholder="请输入" @click-icon="username = ''"/>
			  <van-field v-model="password" label="支付宝账号" placeholder="请输入"/>
			  <div class="butAdd but-80-red" style="margin: 40px auto;" v-on:click="">保存</div>
			</van-cell-group>
		</div>
	</van-popup>
	<van-popup v-model="bankCard" position="bottom" :overlay="true">
		<!--银行卡绑定-->
		<div>
			<h2 style="text-align:center;">银行卡绑定</h2>
			<van-cell-group>
			  <van-field v-model="username" label="户名" icon="clear" placeholder="请输入" @click-icon="username = ''"/>
			  <van-field v-model="password" label="开户行" placeholder="请输入"/>
			  <van-field v-model="password" label="银行卡账号" placeholder="请输入"/>
			  <div class="butAdd but-80-red" style="margin: 40px auto;"  v-on:click="">提交</div>
			</van-cell-group>
		</div>
	</van-popup>
  </div>
</template>

<script>
export default {
  name: 'textHtml',
  props: ['value'],
  data () {
    return {
    	alipay:false,
    	bankCard:false,
    	username:'',
    	password:'',
    	id_num:''
    }
  },
  created:function(){
    setTimeout(function(){
    		$(".van-uploader__input").css("height","100px");
    },500)
  },
  methods: {
  	onRead0: function (file) {
  		this.onRead(file,0);
  	},
  	onRead1: function (file) {
  		this.onRead(file,1);
  	},
  	onRead2: function (file) {
  		this.onRead(file,2);
  	},
    onRead: function (file,num) {
    	 var num = num;
      console.log(file)
      if(!file.file.name){return false;}
      var _this = this;
      $("#msgLoding").show();
		var formData = new FormData();
		formData.append("file", $('.van-uploader__input')[num].files[0]);
		$.ajax({
	        type:'POST',
	        url:_this.apiUrl+'/e/wxapi/guiboweb_editpic.php',
	        data:formData,
	        dataType:"json",
	        cache: false,
	        contentType: false,        /*不可缺*/
	        processData: false,        /*不可缺*/
	        success:function(res){
	        		$("#msgLoding").hide();
	    			$(".butIcons").eq(num).hide();
	    			$(".imgH3").attr("data-id"+num,_this.imgUrl+res.path);
	    			var img = $(".van-uploader").eq(num).find("img").attr("src");
			    if(img){
			      	$(".van-uploader").eq(num).find("img").attr("src",file.content);
			    }else{
			      	var imgHtml = '<img class="img" src="'+file.content+'" style="width:20rem;height:14rem; display: block;margin:0px auto;">'
			      	$(".van-uploader").eq(num).append(imgHtml)
			    }
	        		
	        },
	        error : function(XmlHttpRequest, textStatus, errorThrown) {
	        },
	        complete : function(XMLHttpRequest, textStatus) {
	        }
	   });
    },
    alipayFn: function (type) {
  		this.alipay = type;
  		this.bankCard = false;
  	},
  	bankCardFn: function (type) {
  		this.bankCard = type;
  		this.alipay = false;
  	},
  	//提交实名认证
  	certification:function(){
  		var _this = this;
  		if(localStorage.user){
  	 		var user_id =  JSON.parse(localStorage.user).userid;
  	 	}else{
  	 		_this.utilMsg('请先登录');
  	 		return false;
  	 	}
		var true_name = _this.username;
		var id_num = _this.id_num;
		var zhengmian = $(".imgH3").attr("data-id0");
		var fanmian = $(".imgH3").attr("data-id1");
		var shouchi = $(".imgH3").attr("data-id2");
  		if(!true_name){
  			_this.utilMsg('请输入名字');
  	 		return false;
  		}else if(!id_num){
  			_this.utilMsg('请输入身份证');
  	 		return false;
  		}else if(!zhengmian){
  			_this.utilMsg('请上传身份证正面');
  	 		return false;
  		}else if(!fanmian){
  			_this.utilMsg('请上传身份证反面');
  	 		return false;
  		}else if(!shouchi){
  			_this.utilMsg('请上传手持身份证');
  	 		return false;
  		}
  		var data = {
  			"fk_user_id":user_id,
  			"true_name":_this.username,
  			"id_num":id_num,
  			"zhengmian":zhengmian,
  			"fanmian":fanmian,
  			"shouchi":shouchi,
  		};
  		$("#msgLoding").show();
    		$.post(_this.apiUrl+'/e/wxapi/shiming.php',data,function(res){
			$("#msgLoding").hide();
			if(res.status!==1){
				_this.utilMsg(res.content);
  	 			return false;
			}
    			if(_this.utilIsLocalhost()==false){
				var content = {"type":"add_post_end"}
			    	var datas = JSON.stringify(content);
			    	alertTtf(datas);
		  	 }else{
		  	 	_this.utilMsg('成功')
		  	 }
		},'json')
  	}
  }
}
</script>
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.3rem;background: #fff;overflow: hidden;}
	.addList textarea{width:96%;font-size: 1.3rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;font-size: 1.3rem;}
	.addList input{border: 0;font-size: 1.3rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../../assets/xiangji@3x.png) center 3rem no-repeat;background-size:3rem 3rem;
	}
	.butIcons{
		margin: 0 auto;width:10rem;height:3rem;padding-top:4.5rem;
		text-align:center;color: #ccc;z-index: 1;border: dashed 1px #ccc;
	}
</style>



