<template>
  <div class="indexHtml">
    <div data-v-645028cc="" class="van-nav-bar van-hairline--top-bottom" style="z-index: 1;">
      <div class="van-nav-bar__left" @click="returnLeft">返回</div>
      <div class="van-nav-bar__title">
        添加群聊
      </div>
      <div class="van-nav-bar__right" @click="addGroupChat">
        创建
      </div>
    </div>
    <van-search placeholder="搜索好友" v-model="search" /> {{cheng_yuan}}
    <van-cell v-for="item in list">
      <div class="list-col">
        <div class="img"><img :src="item.head_img" /></div>
        <div>{{item.username}}</div>
        <div class="right">
          <input type="checkbox" name="cheng_yuan" :value="item.userid" @click="setCheckValue($event,item)" />
        </div>
      </div>
    </van-cell>
  </div>
</template>
<script>
$("title").text("添加群聊");
import { Toast } from "vant";
import { List } from "vant";
export default {
  name: "indexHtml",
  data() {
    let _this = this;
    return {
      name: "",
      search: "",
      user: {},
      msg: "",
      input: "",
      message: "",
      list: [],
      loading: true,
      cheng_yuan: []
    };
  },
  created: function() {
    let _this = this;
    if (localStorage.user) {
      _this.user = JSON.parse(localStorage.user);
    } else {
      _this.utilMsg("请先登录");
      return false;
    }
    _this.getList();
  },
  methods: {
    setCheckValue: function(ev, item) {
      var _this = this;
      _this.cheng_yuan = [];
      $("[name='cheng_yuan']:checked").each(function(){
        _this.cheng_yuan.push(parseInt($(this).val()));
      })
      console.info(_this.cheng_yuan);
    },
    //添加好友列表
    getList: function() {
      let _this = this;
      this.loading = true;

      var param = {
        fk_user_id: _this.user.userid, //用户id
        searchStr: _this.search, //搜索字段，用户昵称
        pageIndex: 0,
        pageSize: 15
      };

      $.ajax({
        type: "GET",
        url: _this.apiUrl + "/e/wxapi/selectFriends.php", // 接口返回JSON格式的数据
        cache: false,
        datatype: "json",
        data: param,
        success: function(obj) {
          _this.list = $.parseJSON(obj);
          this.loading = false;
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
          this.loading = false;
        },
        complete: function(XMLHttpRequest, textStatus) {}
      });
    },
    addGroupChat() {
      let _this=this,param = {
        fk_user_id: _this.user.userid, //用户id
        name: "创建群聊" || _this.name, //群聊名称
        cheng_yuan: _this.cheng_yuan.toString() //群聊成员
      };

      $.ajax({
        type: "GET",
        url: _this.apiUrl + "/e/wxapi/add_qunliao.php", // 接口返回JSON格式的数据
        cache: false,
        datatype: "json",
        data: param,
        success: function(obj) {
          console.log(obj);
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {},
        complete: function(XMLHttpRequest, textStatus) {}
      });
    },
    returnLeft: function() {
      let _this = this;
      this.$router.push({
        name: "friends",
        params: { userId: _this.user.userid }
      });
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  background-color: rgb(242, 242, 242);
}
.van-nav-bar .van-nav-bar__title {
  max-width: 200px;
  margin: 0 auto;
  padding-top: 8px;
}
.list-main {
  padding: 5px 0;
  background-color: #fff;
}
.list-main .van-cell {
  padding: 5px 15px;
}
.list-main ~ .list-main {
  margin-top: 10px;
}
.list-col {
  position: relative;
  min-height: 50px;
  padding: 0 50px 0 60px;
  line-height: 50px;
}
.list-col .img {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 50px;
  background-color: #ddd;
  overflow: hidden;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  font-size: 0;
}
.list-col img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}
.list-col .right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}
[name='cheng_yuan']{
  height: 20px;
  width: 20px;
  padding: 0;
  margin: 0;
}
</style>


