<template>
  <div class="indexHtml">
    <div data-v-645028cc="" class="van-nav-bar van-hairline--top-bottom" style="z-index: 1;">
      <div class="van-nav-bar__left" @click="returnLeft">返回</div>
      <div class="van-nav-bar__title">
        我的好友
      </div>
      <div class="van-nav-bar__right" @click="addModal = true;">
        <i class="van-icon van-icon-add-o"></i>
      </div>
    </div>
    <van-actionsheet v-model="addModal" :actions="actions" cancel-text="取消" />
    <van-search placeholder="搜索好友" v-model="search" />
    <van-cell v-for="item in list">
      <div class="list-col">
        <div class="img"><img :src="item.head_img" /></div>
        <div>{{item.username}}</div>
        <div class="right">
          <van-button size="small" @click="addFriends(item.userid)">添加</van-button>
        </div>
      </div>
    </van-cell>
  </div>
</template>
<script>
$("title").text("添加好友");
import { Toast } from "vant";
import { List } from "vant";
export default {
  name: "indexHtml",
  data() {
    let _this = this;
    return {
      addModal: false,
      actions: [
        {
          name: "发起群聊",
          callback: function(){
            _this.hrefTo("/addgroupchat");
          }
        },
        {
          name: "添加好友",
          callback: function(){
            _this.hrefTo("/addfriends");
          }
        },
        {
          name: "消息设置",
          callback: function(){
            _this.hrefTo("/messagesettings");
          }
        }
      ],
      search:"",
      user: {},
      msg: "",
      input: "",
      message: "",
      list: [],
      loading: true
    };
  },
  created: function() {
    let _this = this;
    if (localStorage.user) {
      _this.user = JSON.parse(localStorage.user);
    } else {
      _this.utilMsg("请先登录");
      return false;
    }
    _this.getList();
  },
  methods: {
    //添加好友列表
    getList: function() {
      let _this = this;
      this.loading = true;
      let param = {
        fk_user_id: _this.user.userid, //用户id
        searchStr: _this.search, //搜索字段，用户昵称
        pageIndex: 0,
        pageSize: 15
      };

      $.ajax({
        type: "GET",
        url: _this.apiUrl + "/e/wxapi/searchFriends.php",
        cache: false,
        datatype: "json",
        data: param,
        success: function(obj) {
          _this.list = $.parseJSON(obj);
          this.loading = false;
        },
        error: function(XmlHttpRequest, textStatus, errorThrown) {
          this.loading = false;
        },
        complete: function(XMLHttpRequest, textStatus) {}
      });
    },
    //添加好友
    addFriends: function(id){
      let _this = this;
      var param = {
          fk_user_id:_this.user.userid,
          friend_id: id
      };
      $.ajax({
          type : "GET",
          url :  _this.apiUrl + "/e/wxapi/addFriend.php", // 接口返回JSON格式的数据
          cache : false,
          datatype : 'json',
          data : param,
          success : function(obj) {
            obj = $.parseJSON(obj);
            Toast.success(obj.content);
            _this.getList();
          },
          error : function(XmlHttpRequest, textStatus, errorThrown) {
          },
          complete : function(XMLHttpRequest, textStatus) {
          }
      });

    },
    returnLeft: function(){
      let _this = this;
      this.$router.push({
        name: "friends",
        params: { userId: _this.user.userid }
      });
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.van-nav-bar .van-nav-bar__title {
  max-width: 200px;
  margin: 0 auto;
  padding-top: 8px;
}
.list-col{
  position: relative;
  min-height: 50px;
  padding: 0 50px 0 60px;
  line-height: 50px;
}
.list-col .img{
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 50px;
  background-color: #999;
}
.list-col img{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}
.list-col .right{
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}
</style>



